const myCacheStorage = (name) =>  {
    if ('caches' in self) {
        return {
            name: name,

            openCache() {
                return caches.open(name)
            },

            createCacheRequestFromRawRequest(request) {
                let params;
                if (request && request.headers && request.headers.has('params')) {
                    params = JSON.parse(decodeURIComponent(request.headers.get('params')));
                }

                return this.createCacheRequest(request.url, params ? params : {});
            },

            createCacheRequest(basicURL, params) {
                let cacheId = basicURL + "/";
                if (params) {
                    Object.entries(params).forEach(entry => {
                        let key = entry[0];
                        let value = entry[1];
                        cacheId += `${key}:${value};`;
                    });
                }

                let request = new Request(cacheId);
                request.headers.append('url', encodeURI(basicURL));

                if (params && Object.keys(params).length > 0)
                    request.headers.append('params', encodeURI(JSON.stringify(params)));

                return request;
            },

            add(basicURL, params, response, lifeTime) {
                let request = this.createCacheRequest(basicURL, params);

                response = new Response(response.body, response);
                response.headers.append('expirationDate', new Date().getTime() + (lifeTime * 1000));

                this.openCache().then(cache => {
                    cache.put(request, response);
                });
            },

            async updateJSON(request, data) {
                let cache = await this.openCache(),
                    res = await this.find(request);

                let cacheRequest = this.createCacheRequestFromRawRequest(request);

                if (res) {
                    cache.put(cacheRequest, new Response(JSON.stringify(data), {
                        headers: res.headers,
                    }));
                }
            },

            async get(request) {
                let cache = await this.openCache(),
                    res = await this.find(request);

                if (res) {
                    if (new Date().getTime() - JSON.parse(res.headers.get("expirationDate")) < 0) {
                        res = new Response(res.body, res);
                        res.isFromCache = true;
                        return res
                    } else {
                        cache.delete(request);
                        return null;
                    }
                } else {
                    return null;
                }
            },

            async find(request) {
                let cache = await this.openCache(),
                    requests = await cache.keys(),
                    res;

                for (let i = 0; i < requests.length; i++) {
                    let item = requests[i],
                        itemURL = decodeURIComponent(item.headers.get('url'));

                    //console.log({itemURL, request});

                    if (itemURL === request.url) {

                        if (request.headers.has('params') === item.headers.has('params')) {

                            if (request.headers.has('params')) {
                                let requestParams = JSON.parse(decodeURIComponent(request.headers.get('params'))),
                                    itemParams = JSON.parse(decodeURIComponent(item.headers.get('params'))),
                                    isOk = true;

                                //console.log({requestParams, itemParams});

                                for (let param in requestParams) {
                                    if (!itemParams.hasOwnProperty(param) || itemParams[param] !== requestParams[param]) {
                                        isOk = false;
                                        break;
                                    }
                                }

                                if (isOk) {
                                    //console.log("params are ok");
                                    res = item;
                                    break
                                }
                            } else {
                                res = item;
                                break
                            }
                        }
                    }
                }

                //console.log("find", {request, res});

                if (res) {
                    return await cache.match(res);
                } else {
                    return null
                }
            },

            async has(request) {
                if (typeof request === "string") {
                    request = new Request(request);
                }

                let res = await this.get(request);
                //console.log({request, res});
                return res !== null && res !== undefined;
            },

            clean() {
                caches.delete(name)
                    .then(() => {
                        console.log(`Cache(${name}) successfully deleted!`);
                    })
                    .catch((error) => {
                        console.log(`Error during deleting Cache(${name}): \n${error}`);
                    })
            },

            isSupported() {
                return 'caches' in self
            },
        }
    } else {
        return null;
    }
};

export default myCacheStorage;