import config from "../common/config"
import myCacheStorage from "../common/cache"
import jToken from "../common/jToken"

const cache = myCacheStorage('api');

const APIService = {
    protectPostWithCaching(resource, params, lifeTime) {
        return this.requestWithCaching(config.PROTECTED_API_URL() + resource, params, lifeTime, {method: 'post'})
    },

    protectedGetWithCaching(url, lifeTime) {
        return this.requestWithCaching(url, null, lifeTime, {method: 'get'})
    },

    requestWithCaching(url, rawParams, lifeTime, requestParams) {
        let params, keyParams;
        if (rawParams && 'params' in rawParams && 'keys' in rawParams) {
            params = rawParams.params;
            keyParams = rawParams.keys;
        } else {
            params = keyParams = rawParams
        }

        if (cache !== null) {
            let cacheRequest = new Request(url);

            if (keyParams && Object.keys(keyParams).length > 0)
                cacheRequest.headers.append('params', encodeURI(JSON.stringify(keyParams)));

            return cache.has(cacheRequest).then(has => {
                if (has) {
                    return cache.get(cacheRequest).then(res => res);
                } else {
                    return this.protected(url, params, requestParams)
                        .then((res) => {
                            if (res) {
                                cache.add(url, keyParams, res.clone(), lifeTime);
                                return res
                            }
                            return null;
                        })
                }
            });
        } else {
            return this.protected(url, params, requestParams);
        }
    },

    protectedPost(resource, params) {
        return this.protected(config.PROTECTED_API_URL() + resource, params, {method: 'post'})
    },

    protected(url, params, requestParams) {
        let requestSettings = {
            ...requestParams,
            headers: this.getHeaders(),
        };
        if (params) {
            requestSettings.body = JSON.stringify(params);
        }

        return fetch(url, requestSettings)
            .then((res) => {
                if (res.ok) {
                    return res
                } else if (res.status === 401 ) {
                    jToken.delete();
                    document.location.reload();
                    return {ok: false}
                } else {
                    return res
                }
            })
    },

    postRequest(resource, params) {
        return this.request(config.API_URL() + resource, params, {method: 'post'})
    },

    request(url, params, requestParams) {
        let requestSettings = {
            ...requestParams,
        };
        if (params) {
            requestSettings.body = JSON.stringify(params);
        }

        return fetch(url, requestSettings)
    },

    getHeaders() {
        const headers = new Headers();
        headers.append('Access-Control-Allow-Origin', '*');
        headers.append('Authorization', jToken.get());

        return headers;
    }
};

export default APIService;